import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { MaterialIssueReportRoutingModule } from './material-issue-report-routing.module';
import { MaterialIssueReportComponent } from './material-issue-report.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    MaterialIssueReportRoutingModule
  ],
  declarations: [
    MaterialIssueReportComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class MaterialIssueReportModule { }



