import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MaterialIssueReportComponent } from './material-issue-report.component';

const routes: Routes = [
    {
        path: '', component: MaterialIssueReportComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MaterialIssueReportRoutingModule { }



